import React from 'react';
import PropTypes from 'prop-types';
import Panel from '@vkontakte/vkui/dist/components/Panel/Panel';
import PanelHeader from '@vkontakte/vkui/dist/components/PanelHeader/PanelHeader';
import PanelHeaderButton from '@vkontakte/vkui/dist/components/PanelHeaderButton/PanelHeaderButton';
import Div from '@vkontakte/vkui/dist/components/Div/Div';
import Separator from '@vkontakte/vkui/dist/components/Separator/Separator';
import Cell from '@vkontakte/vkui/dist/components/Cell/Cell';
import Avatar from '@vkontakte/vkui/dist/components/Avatar/Avatar';
import Icon28MoneyCircleOutline from '@vkontakte/icons/dist/28/money_circle_outline';
import Icon20EducationOutline from '@vkontakte/icons/dist/20/education_outline';
import FixedLayout from '@vkontakte/vkui/dist/components/FixedLayout/FixedLayout';
import Search from '@vkontakte/vkui/dist/components/Search/Search';

import {
	Group, Header, List, InfoRow, Counter 
} from '@vkontakte/vkui/';

const Oservise = ({ go, id}) => (
	<Panel id={id}>
        	<PanelHeader
			left={<PanelHeaderButton>
			</PanelHeaderButton>}
		>
		    <small>Категории</small>
		</PanelHeader>
		<FixedLayout vertical="top">
            <Search />
		<Group>
        <List>
                <Cell expandable
					before={<Avatar size={45} style={{
						background: '#6ba6f6'
					}}><Icon20EducationOutline  fill='#fff' width={30} height={30}/></Avatar>}  onClick={go} data-to="forschool" >Лайфхаки для школы</Cell> 
                <Separator />

				<Cell expandable before={<Avatar size={45} style={{
						background: '#51b651'
					}}><Icon28MoneyCircleOutline fill='#fff' width={30} height={30}/></Avatar>} onClick={go} data-to="forhome" >Лайфхаки для дома</Cell>
                <Separator />
                <Cell expandable before={<Avatar size={45} style={{
						background: '#ffa000'
					}}><Icon28MoneyCircleOutline fill='#fff' width={30} height={30}/></Avatar>} onClick={go} data-to="forthecar" >Лайфхаки для машины</Cell> 
                <Separator />
                <Cell expandable before={<Avatar size={45} style={{
						background: '#ff3347'
					}}><Icon28MoneyCircleOutline fill='#fff' width={30} height={30}/></Avatar>} indicator={<Counter>В разработке</Counter>}>Лайфхаки для лета</Cell>
                <Separator />
                <Div style={{ paddingTop: 0, paddingBottom: 0, color: 'grey' }}>
				<p align="center">Скоро тут появятся новые категории.</p>
				</Div>
                <Separator />
                </List>
                </Group>
				</FixedLayout>
		</Panel>
	
	
	);


Oservise.propTypes = {
	id: PropTypes.string.isRequired,
	go: PropTypes.func.isRequired,
	any: PropTypes.func,
	fetchedUser: PropTypes.shape({
		photo_200: PropTypes.string,
		first_name: PropTypes.string,
		last_name: PropTypes.string,
		city: PropTypes.shape({
			title: PropTypes.string,
		}),
	}),
};

export default Oservise;
